import { Box, Chip, lighten } from "@rp/react/web/mui/core"
import { WatchlistsTab } from "../hooks/useWatchlistsTabNavigation"

type Props = {
  canShare: boolean
  tabValue: WatchlistsTab
  handleChange: (newValue: WatchlistsTab) => void
  organizationName: string
}

export const WatchlistsCardViewTabs = ({
  canShare,
  tabValue,
  handleChange,
  organizationName,
}: Props) => {
  return (
    <Box
      sx={theme => ({
        gap: 1.5,
        display: "flex",
        "& .MuiChip-root": {
          fontSize: 14,
          backgroundColor: theme.palette.overlay.surface,
        },
        "& .MuiChip-root:hover": {
          cursor: "pointer",
          backgroundColor: lighten(theme.palette.background.default, 0.2),
        },
        "& .MuiChip-root.selected": {
          backgroundColor: theme.palette.primary.main,
          color: theme.palette.primary.contrastText,
        },
        "& .MuiChip-root.selected:hover": {
          backgroundColor: theme.palette.primary.main,
          cursor: "default",
        },
      })}
    >
      <Chip
        label="Favorites"
        className={tabValue === "favorites" ? "selected" : undefined}
        onClick={() => handleChange("favorites")}
      />
      <Chip
        label="My Watchlists"
        className={tabValue === "my-watchlists" ? "selected" : undefined}
        onClick={() => handleChange("my-watchlists")}
      />
      {canShare && (
        <Chip
          label={organizationName ?? "Company's Watchlists"}
          className={tabValue === "public-watchlists" ? "selected" : undefined}
          onClick={() => handleChange("public-watchlists")}
          sx={{ maxWidth: "200px" }}
        />
      )}
      <Chip
        label="Community"
        className={tabValue === "community-watchlists" ? "selected" : undefined}
        onClick={() => handleChange("community-watchlists")}
      />
      <Chip
        label="Thematic"
        className={tabValue === "thematic-watchlists" ? "selected" : undefined}
        onClick={() => handleChange("thematic-watchlists")}
      />
    </Box>
  )
}
